@import url('https://fonts.googleapis.com/css?family=Merriweather');

body {
  margin: 0;
  padding: 0;
  color: #333;
  background: #F7F7F7;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  font-weight: 200;
}

ul{
  /* list-style-type: square; */
  /* list-style-position: inside; */
  padding-left: 0;
  list-style: none;
}

ul li{
  display: inline-block;
  margin-right: 20px;
}

ul li:before{
  color: #999;  
  font-size: 1em; 
  padding-right: 8px; 
  position: relative;
  top: 0em; 
}

ul li:first-child:before{
  left: -3px;
  padding-right: 7;
}

ul li:nth-child(odd):before{
  content: "\25FD"; 
}

ul li:nth-child(even):before{
  color: #d9d9d9;
  content: "\25CF"; 
  font-size: 0.8em;
  padding-left: 2px;
  top: -1px;
}

h1{
  font-family: 'Merriweather', serif;
  font-size: 50px;
  text-align: left;
  line-height: 52px;
  font-weight: bold;
}

h2{
  font-weight: 300;
  font-size: 20px;
}

h3{
  font-weight: 600;
  font-size: 25px;
}

h4{
  font-weight: 500;
  font-size: 22px;
  margin-bottom: 15px;
}

.container{
  display: -ms-flexbox;
  display: flex;
}

.sidebar{
  width: 360px;
  background: #FFF;
  background-image: url('./matheus-baumgart@2x.png');
  background-size: 360px 460px;
  background-repeat: no-repeat;
  text-align: left;
  border-right: 1px solid #ececec;
}

.sidebar .sidebar-container{
  padding: 335px 40px 40px;
}

.main-col{
  padding: 60px;
  max-width: 882px;
}

.main-col > h3 {
  margin-top: 0;
}

.color-black{
  font-weight: 800;
  color: #000;
}

.color-333{
  color: #333;
}

.text-bolder{
  font-weight: 700;
}

.text-500{
  font-weight: 500;
}

.work-item{
  margin-bottom: 60px;
}

.work-subtitle{
  color: #949494;
  font-size: 16px;
  margin-top: 5px;
}

.work-desc{
  font-size: 16px;
}

